/* Remove the default list-style for the menu items */
.menu {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding: 17px;
    margin: 10px;
    list-style: none;
    background-color: #f2f2f2; 
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-weight: bold;
}

/* Use a bigger font size and capitalize the text */
.menu li {
    font-size: 32px;
    text-transform: lowercase;
    margin-right: 40px;
}

/* Remove the default underline for links */
.menu a {
    color: black;
    text-decoration: none;
}

/* Styles for menu items when the viewport width is below 600px */
@media only screen and (max-width: 600px) {
    .menu {
        flex-direction: column;
        align-items: flex-start;
    }

    .menu li {
        font-size: 24px;
        margin-right: 0;
        margin-bottom: 5px;
    }
}